<template>
    


    <card class="card" title="">

      <template slot="header">
        <h4 class="card-title">Tipo de importe <router-link class="pull-right" :to="{path:'/conceptoimportes/crear'}"><i class="ti ti-plus"></i></router-link></h4>
      </template>


      <div>
    
        <table class="table">

          <thead>
            <th>ID</th>
            <th>Clave</th>
            <th>Costo enlazado</th>
            <th>Habilitado</th>
            <th>Acciones</th>

          </thead>

          <tbody>

            <tr v-for="(item,index) in data" :key="index">
              
              <td>
                {{ item.id }}

              </td>
              <td>
                {{ item.concepto }}

              </td>
              <td>
                {{ item.tipo=='envio'?'Costos de Amazon':'Costo del producto' }}
              </td>
              <td>
                {{ item.activo=='1'?'Si':'No' }}
              </td>
              <td class="actions">

                  <router-link :to="{path:'/conceptoimportes/editar/'+item.id}"><i class="ti-pencil"></i></router-link>
                  <a @click="deleteMe(item.id)" href="javascript:void(0)"><i class="ti-trash"></i></a>

              </td>
            
            </tr>


          </tbody>

        </table>

    </div>
    </card>


</template>


<script>



import api from '@/util/api';

export default{

    name:"Conceptos",
    components:{
    },
    data(){

        return{

          data:[]
    
    
        }
      },

      methods:{



        getItems(){
            api.get('/conceptoimportes/').then(response=>{
              this.data=response.data;
              

            });
        },
        deleteMe(id){

          if(confirm('Â¿Desea eliminar el registro?')){
            api.delete('/conceptoimportes/'+id).then(()=>{

              this.getItems();
            }).catch(error=>{

              console.log(error);

            });
          }

          
        }

      },

      mounted(){

        this.getItems();
        

      }

    

}

</script>